describe(' switchToSingle   SimpleJsSliderView', () => {
  test('ThumbTwo   null', () => {
    view.switchToSingle();
    expect(view['thumbTwo']).toBeNull();
  });
});

describe(' switchToRange   SimpleJsSliderView', () => {
  test('ThumbTwo    null', () => {
    view.switchToRange();
    expect(view['thumbTwo']).not.toBeNull();
  });
  test('ThumbTwo    null', () => {
    view.switchToSingle();
    view.switchToRange();
    expect(view['thumbTwo']).not.toBeNull();
  });
  test('ThumbTwo    null,  popUpTwo   null', () => {
    view.switchToSingle();
    view['popUpOne'] = null;
    view.switchToRange();
    expect(view['thumbTwo']).not.toBeNull();
    expect(view['popUpTwo']).toBeNull();
  });
});

describe(' disablePopUps   SimpleJsSliderView', () => {
  test('PopUpOne   null', () => {
    view.disablePopUps();
    expect(view['popUpOne']).toBeNull();
  });
  test('PopUpTwo   null', () => {
    view.switchToRange();
    view.disablePopUps();
    expect(view['popUpTwo']).toBeNull();
  });
});

describe(' enablePopUps   SimpleJsSliderView', () => {
  test('PopUpOne    null', () => {
    view.disablePopUps();
    view.enablePopUps();
    expect(view['popUpOne']).toBeDefined();
  });
  test('PopUpTwo   null', () => {
    view.switchToSingle();
    view.enablePopUps();
    expect(view['popUpTwo']).toBeNull();
  });
  test('PopUpTwo    null', () => {
    view.switchToSingle();
    view.switchToRange();
    view.disablePopUps();
    view.enablePopUps();
    expect(view['popUpTwo']).toBeDefined();
  });
});

describe(' disableScale   SimpleJsSliderView', () => {
  test('Scale   ', () => {
    view.disableScale();
    expect(view['scale']).toBeNull();
  });
  test('Scale   ', () => {
    view.enableScale();
    view.disableScale();
    expect(view['scale']).toBeNull();
  });
});

describe(' enableScale   SimpleJsSliderView', () => {
  test('Scale   ', () => {
    view.enableScale();
    view.enableScale();
    expect(view['scale']).toBeDefined();
  });
  test('Scale   ', () => {
    view.disableScale();
    view.enableScale();
    expect(view['scale']).toBeDefined();
  });
});

