describe(' updateThumbs   SimpleJsSliderView', () => {
  test(' moveTo  thumbOne     ', () => {
    const spy = spyOn(view['thumbOne'], 'moveTo');
    view.updateThumbs({
      thumbOne: { left: 0, top: 0 },
      thumbTwo: { left: 0, top: 0 },
    });
    expect(spy).toBeCalledTimes(1);
  });
  test(' moveTo  thumbTwo    ', () => {
    if (view['thumbTwo'] !== null) {
      const spy = spyOn(view['thumbTwo'], 'moveTo');
      view.updateThumbs({
        thumbOne: { left: 0, top: 0 },
        thumbTwo: null,
      });
      expect(spy).toBeCalledTimes(0);
    }
  });
  test(' moveTo  thumbOne     ', () => {
    if (view['thumbTwo'] !== null) {
      const spy = spyOn(view['thumbTwo'], 'moveTo');
      view.updateThumbs({
        thumbOne: { left: 0, top: 0 },
        thumbTwo: { left: 0, top: 0 },
      });
      view.updateThumbs({
        thumbOne: { left: 0, top: 0 },
        thumbTwo: { left: 0, top: 0 },
      });
      expect(spy).toBeCalledTimes(2);
    }
  });
});

